@import "../../../assets/css/ultilities/variable";
.container {
  width: 100%;
  padding-top: 2.0625em;
  padding: 2.0625em 1.25em;
  &__title {
    color: $blue-text;
    font-weight: 600;
    font-size: 1.5625em;
  }
  &__sub-title {
    color: $blue-text;
    font-size: 1.25em;
    font-weight: 400;
    margin-top: 1.0625em;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    width: 100%;
    min-height: 100vh;
    &__title {
      font-size: 3rem;
      margin-top: 62px;
      padding-top: 80px;
    }
    &__sub-title {
      font-size: 25px;
      font-weight: 400;
      margin-top: 32px;
      padding-top: 11px;
      margin-bottom: 4em;
    }
  }
}
@media screen and (min-width: $hd-size) {
  .container {
    width: 100%;
    &__title {
      font-size: 45px;
      margin-top: 62px;
    }
    &__sub-title {
      font-size: 25px;
      font-weight: 400;
      margin-top: 32px;
    }
  }
}
