@import "../../../assets/css/ultilities/variable";

.container {
  width: 100%;

  &__input-field {
    display: block;
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid $blue-border;
    background-color: $weak-blue-bg-color;
    margin-top: 9px;
    padding-left: 7px;
    color: $blue-text;
  }

  input::placeholder {
    color: $blue-text;
  }

  &__checkbox {
    margin-top: 23px;
    width: 100%;
    font-size: 10px;
    font-weight: 400;

    input {
      flex: 1;
      justify-content: flex-start;
      margin-top: 2px;
    }

    label {
      flex: 9;
      color: $blue-text;
    }
    &-item {
      margin-top: 13px;
      display: flex;
    }
  }

  button {
    width: 110px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid $blue-border;
    background-color: $weak-blue-bg-color;
    color: $blue-text;
    font-size: 10px;
    font-weight: 400;
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    transition: 100ms;
    &:hover {
      color: $white-text;
      background-color: $strong-blue-bg-color;
      border: 1px solid #fff;
    }
  }
}

@media screen and (min-width: $mobile-size) {
  .container {
    width: 252px;

    &__input-field {
      display: block;
      width: 252px;
      height: 30px;
      border-radius: 5px;
      border: 1px solid $blue-border;
      background-color: $weak-blue-bg-color;
      margin-top: 9px;
      padding-left: 7px;
      color: $blue-text;
    }

    input::placeholder {
      color: $blue-text;
    }

    &__checkbox {
      margin-top: 23px;
      width: 252px;
      font-size: 10px;
      font-weight: 400;

      input {
        flex: 1;
        justify-content: flex-start;
        margin-top: 2px;
      }

      label {
        flex: 9;
      }
      &-item {
        margin-top: 13px;
        display: flex;
      }
    }

    button {
      width: 110px;
      height: 30px;
      border-radius: 5px;
      border: 1px solid $blue-border;
      background-color: $weak-blue-bg-color;
      color: $blue-text;
      font-size: 10px;
      font-weight: 400;
      display: block;
      margin: 0 auto;
      margin-top: 15px;
    }
  }
}
